import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import {MatSort} from '@angular/material/sort';
import { CustomerService } from '../services/customer.service';
import { Customer, Convert } from '../models/customer';
import { DatePipe } from '@angular/common';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router'
import Swal from 'sweetalert2';
import { ISpinnerConfig, SPINNER_ANIMATIONS, SPINNER_PLACEMENT } from '@hardpool/ngx-spinner';
import { ImageviewComponent } from '../imageview/imageview.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  mySubscription: any;
  constructor(private customerService: CustomerService, public _router: Router, private activatedroute: ActivatedRoute, public dialog: MatDialog, private _cdr: ChangeDetectorRef) {
    this._router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
    this.mySubscription = this._router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        // Trick the Router into believing it's last link wasn't previously loaded
        this._router.navigated = false;
      }
    });

  }

  teller_id: string;
  customer: Customer;
  profile_image_url: string = 'http://115.84.105.130:8081/api/kyc/profileImageByCif/'
  doc_image_url: string = "http://115.84.105.130:8081/api/kyc/doc/"
  customerList: Customer[] = [];
  current_index: number = 0;


  displayedColumns: string[] = ['CIF', 'STATUS', 'LANAME', 'JOB', 'ACTION'];
  dataSource = new MatTableDataSource();

  isShowSpinner: boolean;
  spinnerConfig: ISpinnerConfig;

  branch_name: string = '';


  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;


  ngOnInit() {

    this.paginator._intl.itemsPerPageLabel = "ຈຳນວນແຖວຕໍ່ໜ້າ:";
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    // this.activatedroute.params.subscribe(data => {
    //   this.teller_id = data.teller;
    // })
    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.rotating_dots,
      size: "3rem",
      color: "#00c979"
    };

    this.showSpinner()
    this.feedAllCustomerList()
  }


  openImageDialog(imagepath: string) {
    const dialogRef = this.dialog.open(ImageviewComponent,
      {
        height: '90%',
        width: '80%',
        'data': { 'image': imagepath }
      });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }


  showSpinner() {
    this.isShowSpinner = true;
  }

  hideSpinner() {
    this.isShowSpinner = false;
  }


  getTellerIDString(): string {
    return localStorage.getItem('teller_id');
  }


  ngOnDestroy(): void {
    console.log('on destroy')
    localStorage.setItem('session', 'false')
  }

  feedAllCustomerList() {
    console.log('0o0oo0o0k')
    this.customerService.getBranch()
    this.customerService.feedAllCustomerKYC().subscribe((res) => {
      this.hideSpinner()
      this.customerList = res;
      console.log(res.toString)

      this.branch_name = this.customerList[0].branchid.brnNameLA;

      // this.dataSource = new MatTableDataSource<Customer>(this.customerList);
      this.dataSource.data = this.customerList
    }, (err) => {
      console.log(err)
      this.hideSpinner()
    });
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  pipe = new DatePipe('en-US');
  formatDate(val: string): string {
    let newDate = new Date(val);
    const myFormattedDate = this.pipe.transform(newDate, 'dd-MM-yyyy');
    return myFormattedDate;
  }

  getStatusString(val): string {
    let str = "";

    if (val == 1) {
      str = "ລໍຖ້າອະນຸມັດ";
    } else if (val === 2) {
      str = "ອະນຸມັດແລ້ວ";
    } else if (val === 3) {
      str = "ຖືກປະຕິເສດ";
    }
    return str;
  }

  getBackColorStyle(val): string {
    let color_val = "";

    if (val == 1) {
      color_val = "#defaff";
    } else if (val === 2) {
      color_val = "#d1ffd6";
    } else if (val === 3) {
      color_val = "#ffdee3";
    }
    return color_val;
  }

  getForegroundColorStyle(val): string {
    let color_val = "";

    if (val == 1) {
      color_val = "#00a2bf";
    } else if (val === 2) {
      color_val = "#109101";
    } else if (val === 3) {
      color_val = "#cf0011";
    }
    return color_val;
  }


  onLogout() {
    localStorage.setItem('session', 'false');

    Swal.fire({
      title: 'ທ່ານໝັ້ນໃຈບໍ່ວ່າ?',
      text: "ທ່ານຕ້ອງການອອກຈາກລະບົບ!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#45bf57',
      cancelButtonColor: '#d33',
      confirmButtonText: 'ຕົກລົງ, ອອກຈາກລະບົບເລີຍ!',
      cancelButtonText: 'ຍົກເລີກ'
    }).then((result) => {
      if (result.value) {
        this._router.navigate(['/login'])
      }
    })

  }


  onViewDetail(index: number) {
    this.current_index = index;

    this.customer = this.customerList[this.current_index];
    
  }


  onRefreshViewDetail() {
    //location.reload()
    // setTimeout(() => {
    //   this.customer = this.customerList[this.current_index];
    //   this._cdr.detectChanges()
    // }, 300);
    this.feedAllCustomerList()
    this.customer = this.customerList[this.current_index];
  }

  onApprove() {
    if (this.customer == null) {

    } 
    else if (this.customer.approveStatus == 2) {
      Swal.fire({
        icon: 'error',
        title: 'ຜິດພາດ...',
        text: 'ລູກຄ້າທ່ານນີ້ໄດ້ຖືກອະນຸມັດແລ້ວ',
      })
    } else if (this.customer.approveStatus == 3) {
      Swal.fire({
        icon: 'error',
        title: 'ຜິດພາດ...',
        text: 'ລູກຄ້າທ່ານນີ້ໄດ້ຖືກປະຕິເສດແລ້ວຈະບໍ່ສາມາດອະນຸມັດໄດ້',
      })
    }
    else
      Swal.fire({
        title: 'ທ່ານໝັ້ນໃຈບໍ່ວ່າ?',
        text: "ທ່ານຕ້ອງການອະນຸມັດລູກຄ່້າທ່ານນີ້!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#45bf57',
        cancelButtonColor: '#d33',
        confirmButtonText: 'ຕົກລົງ, ອະນຸມັດເລີຍ!',
        cancelButtonText: 'ຍົກເລີກ'
      }).then((result) => {
        if (result.value) {
          this.customerService.approveCustomerKYC(this.customer.cif, this.getTellerIDString()).subscribe((res) => {

            Swal.fire(
              'ອະນຸມັດສຳເລັດ!',
              'ລູກຄ້າທ່ານນີ້ຖືກອະນຸມັດການລົງທະບຽນ e-KYC ເປັນທີ່ຮຽບຮ້ອຍ.',
              'success'
            )
            this.customerService.sendMessageNotification(`ຂໍສະແດງຄວາມຍິນດີ, ການລົງທະບຽນ APB e-KYC ຂອງທ່ານຖືກອະນຸມັດສຳເລັດແລ້ວ`, this.customer.phone)
            this. onRefreshViewDetail()
          }, (err) => {
            console.log(err)
          }
          )

        }
      })

  }
  onReject() {

    if (this.customer == null) {

    } 
    else if (this.customer.approveStatus == 2) {
      Swal.fire({
        icon: 'error',
        title: 'ຜິດພາດ...',
        text: 'ລູກຄ້າທ່ານນີ້ໄດ້ຖືກອະນຸມັດແລ້ວຈະບໍ່ສາມາດປະຕິເສດ ໄດ້',
      })
    } else if (this.customer.approveStatus == 3) {
      Swal.fire({
        icon: 'error',
        title: 'ຜິດພາດ...',
        text: 'ລູກຄ້າທ່ານນີ້ໄດ້ຖືກປະຕິເສດແລ້ວ',
      })
    }
    else
      Swal.fire({
        title: 'ທ່ານໝັ້ນໃຈບໍ່ວ່າ?',
        text: "ທ່ານຕ້ອງການປະຕິເສດລູກຄ່້າທ່ານນີ້!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#45bf57',
        cancelButtonColor: '#d33',
        confirmButtonText: 'ຕົກລົງ, ປະຕິເສດເລີຍ!',
        cancelButtonText: 'ຍົກເລີກ'
      }).then((result) => {
        if (result.value) {
          this.customerService.rejectCustomerKYC(this.customer.cif, this.getTellerIDString()).subscribe((res) => {

            Swal.fire(
              'ປະຕິເສດສຳເລັດ!',
              'ລູກຄ້າທ່ານນີ້ຖືກປະຕິເສດການລົງທະບຽນ e-KYC ເປັນທີ່ຮຽບຮ້ອຍ.',
              'success'
            )
            this.customerService.sendMessageNotification('ການລົງທະບຽນ APB e-KYC ຂອງທ່ານຖືກປະຕິເສດ ເນື່ອງຈາກຂໍ້ມູນທີ່ສົ່ງໄປບໍ່ຖືກຕ້ອງ, ກະລຸນາລົງທະບຽນໃໝ່ອີກຄັ້ງ', this.customer.phone)
            this. onRefreshViewDetail()
          }, (err) => {
            console.log(err)
          }
          )

        }
      })

  }

  getArticleGenderString(gender: string): string {
    let article: string = ''
    if (gender == 'ຊາຍ') {
      article = 'ທ.'
    } else if (gender == 'ຍິງ') {
      article = 'ນ.'
    }
    return article;
  }

}