import { Component, OnInit } from '@angular/core';
import { AuthenService }  from '../services/authen.service';
import { Router } from '@angular/router';
import { SPINNER_ANIMATIONS, SPINNER_PLACEMENT, ISpinnerConfig } from '@hardpool/ngx-spinner';
import Swal from 'sweetalert2';
import { FormGroup,Validators, FormControl  } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {


  _tellerid:string;
  _password:string;

  isShowSpinner: boolean;
  spinnerConfig: ISpinnerConfig;  
  loginForm: FormGroup;

  submitted = false;


  constructor( public authService:AuthenService, public _router:Router) { }

  ngOnInit(): void {



    this.loginForm = new FormGroup({
      teller_id: new FormControl('',[Validators.required, Validators.pattern('^[0-9]+$')]),
      password: new FormControl('',Validators.required)
    })

    // this.loginForm = this.formBuilder.group(
    //   {
    //     teller_id: ['',[Validators.required, Validators.pattern('^[0-9]+$'  )]],
    //     password:['',Validators.required]
    //   }
    // )


    this.spinnerConfig = {
      placement: SPINNER_PLACEMENT.block_window,
      animation: SPINNER_ANIMATIONS.rotating_dots,
      size: "3rem",
      color: "#3ec964"
  };


  }

  get f() { return this.loginForm.controls; }


  onSubmit(){
    this.submitted = true;
    if(this.loginForm.invalid) {
      return;
    }else{
      console.log('valid')
      this.onLogin()
    }

  }


  showSpinner(){
    this.isShowSpinner = true;
  }
  
  hideSpinner(){
    this.isShowSpinner = false;
  }

  onLogin(){
    this.showSpinner()
    this.authService.login({'tellerid':this._tellerid,'password':this._password}).subscribe((res)=>{
      console.log(res)
      if(res.status == false){
        this.hideSpinner()
        Swal.fire({
          icon: 'error',
          title: 'ຜິດພາດ...',
          text: 'ເຂົ້າສູ່ລະບົບບໍ່ສຳເລັດ',
        })
      }else{
     localStorage.setItem('session','true');
     localStorage.setItem('teller_id',this._tellerid);
      Swal.fire({
        position: 'top-end',
        icon: 'success',
        title: 'ເຂົ້າສູ່ລະບົບສຳເລັດ',
        showConfirmButton: false,
        timer: 1500
      })
      this._router.navigate(['/home'])
      }

    },
    (err)=>{
      this.hideSpinner()
      Swal.fire({
        icon: 'error',
        title: 'ຜິດພາດ...',
        text: 'ເຂົ້າສູ່ລະບົບບໍ່ສຳເລັດ',
      })
    
      console.log(err)
    }
    )

  }

}
