import { Injectable } from '@angular/core';
import { from, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Customer } from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  BASE_URL= 'http://115.84.105.130:8081/api'

  constructor(private http: HttpClient) { }

  feedAllCustomerKYC(): Observable<Customer[]> {
    try {
      return this.http.get<Customer[]>(`${this.BASE_URL}/kyc/list/${this.getBranch()}`);
    } catch (e) {
      return null;
    }

  }

  approveCustomerKYC(cif: string,teller_id : string) {

    const formData = new FormData();
    formData.append('tellerid', teller_id);
    try {
      return this.http.put<Customer[]>(`${this.BASE_URL}//kyc/approve/${cif}`,formData);
    } catch (e) {
      return null;
    }
  }
  rejectCustomerKYC(cif: string,teller_id:string) {
    const formData = new FormData();
    formData.append('tellerid', teller_id);
    try {
      return this.http.put<Customer[]>(`${this.BASE_URL}/kyc/reject/${cif}`,formData);
    } catch (e) {
      return null;
    }
  }

  getBranch():string{
    let teller_id = localStorage.getItem('teller_id');
    let branch_id = teller_id.substring(0,5)
    console.log(branch_id)
    return branch_id;
  }

  sendMessageNotification(content:string,phone_num:string){
    return this.http.get<any>(`http://10.1.12.48:1013/api/data?content=${content}&phonenumber=${phone_num}`).subscribe(
      (res)=>{
        console.log(res);
      },
      (err)=>{
        console.log(err)
      }
    )
  }


}
